//FONTS MIXINS
@mixin fontpath($name, $name1, $file) {
	src: local($name), 
		local($name1), 
		url("../fonts/" + $file + ".woff2") format("woff2"), 
		url("../fonts/" + $file + ".woff") format("woff");
}
@mixin fontdefault($style, $weight, $family: null) {
	@if $family != null {
		font-family: $family;
	} @else {
		font-family: $lato;
	}

	font-style: $style;
	font-weight: $weight;
}

//ADD FONTS
//LATO
//thin
@font-face {
	@include fontdefault(normal, 100);
	@include fontpath('Lato Hairline', 'Lato-Hairline', lato-hairline);
}
@font-face {
	@include fontdefault(italic, 100);
	@include fontpath('Lato Hairline Italic', 'Lato-HairlineItalic', lato-hairlineitalic);
}
//light
@font-face {
	@include fontdefault(normal, 300);
	@include fontpath('Lato Light', 'Lato-Light', lato-light);
}
@font-face {
