}
@font-face {
	@include fontdefault(italic, 400);
	@include fontpath('Lato Italic', 'Lato-Italic', lato-italic);
}
//bold
@font-face {
	@include fontdefault(normal, 700);
	@include fontpath('Lato Bold', 'Lato-Bold', lato-bold);
}
@font-face {
	@include fontdefault(italic, 700);
	@include fontpath('Lato Bold Italic', 'Lato-BoldItalic', lato-bolditalic);
}
//black
